<template>
    <div id='map'></div>
</template>

<script>
/* global mapboxgl:true */
/* eslint no-undef: "error" */
const axios = require('axios');
const _ = require('lodash');

const instance = axios.create({
  baseURL: 'https://cloud-shuttle.herokuapp.com',
});
export default {
  name: 'HelloWorld',
  data() {
    return {
      error: '',
      cabs: {},
      map: null,
    };
  },
  props: {
    searchTxt: String,
    me: Object,
  },
  async mounted() {
    this.mapInit();
    await this.feed();
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition();
    } else {
      // eslint-disable-next-line no-console
      console.log('Geolocation is not supported.');
    }
    // this.mapInit();
    // console.log('this.me ', this.me.mapToken);
  },
  methods: {
    mapInit() {
      // console.log(p.coords, '????');
      mapboxgl.accessToken = this.me.mapToken;
      const mapOpt = {
        style: 'mapbox://styles/mapbox/streets-v9',
        center: [77.42, 12.51],
        zoom: 11.15,
        container: 'map',
      };
      const map = new mapboxgl.Map(mapOpt);
      const geoLocate = new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true,
        },
        trackUserLocation: true,
      });
      map.addControl(geoLocate);

      // this.$emit('map-init', map);
      map.on('load', () => {
        this.createLayer();
      });
      this.map = map;
      return map;
    },
    createLayer() {
      const features = [];
      this.cabs.data.forEach((pin) => {
        features.push({
          type: 'Feature',
          geometry: {
            type: 'Point',
            coordinates: [pin.geo.now.long, pin.geo.now.lat],
          },
          properties: {
            description: pin.tweet.msg.text,
            icon: 'bicycle',
          },
        });
      });
      const source = {
        type: 'geojson',
        data: {
          type: 'FeatureCollection',
          features,
        },
      };
      // map.addSource('pins', { type: 'geojson', data: source });
      this.map.addLayer({
        id: 'places',
        type: 'symbol',
        source,
        layout: {
          'icon-image': '{icon}-15',
          'icon-allow-overlap': true,
        },
      });
      this.map.on('click', 'places', (e) => {
        const coordinates = e.features[0].geometry.coordinates.slice();
        const { description } = e.features[0].properties;

        // Ensure that if the map is zoomed out such that multiple
        // copies of the feature are visible, the popup appears
        // over the copy being pointed to.
        while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
          coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
        }

        new mapboxgl.Popup()
          .setLngLat(coordinates)
          .setHTML(description)
          .addTo(this.map);
      });
    },
    async feed() {
      const reqParam = { headers: { Authorization: this.me.token } };
      reqParam.params = {
        searchText: this.searchTxt,
      };
      const cabData = await instance.get('/users', reqParam);
      // console.log('cabData ', cabData);
      this.cabs = cabData.data;
      this.createLayer();
    },
  },
  watch: {
    searchTxt: _.debounce(function search() {
      this.feed();
      this.map.removeLayer('places');
      this.map.removeSource('places');
    }, 1000),
  },
};
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style>
  #map { top:0; bottom:0; width:100%;height: 100vh; }
  .mapboxgl-canvas{ position: relative !important;}
  .mapboxgl-popup {
    color: #0a4b3e;
    max-width: 800px;
    font: 12px/20px 'Helvetica Neue', Arial, Helvetica, sans-serif;
  }
</style>
